@import "../../SCSS/generic/reset";
@import "../../SCSS/elements/colors";

.focus-in-expand {
  animation: focus-in-expand 0.8s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  color: ghostwhite;
  font-size: 3rem;
  justify-content: center;
  align-items: center;
  display: flex;
  width: 100%;
  height: 6.25rem;
  padding-top: 3rem;
}

.fridge {
  width: 45%;
  height: 47rem;
  padding-left: 6.25rem;
  padding-bottom: 3.2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;


}

.backDoor {
  animation: bounce-in-top 1.1s both;
  background-color: map_get($colors, "sun");
  position: relative;
  width: 15.5rem;
  height: 33rem;
  border-radius: 50px 50px 10px 10px;
  border: 6px solid map_get($colors, "sun");
  box-shadow: 13px -9px 47px -16px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 13px -9px 47px -16px rgba(0, 0, 0, 0.75);
  box-shadow: 13px -9px 47px -16px rgba(0, 0, 0, 0.75);
}

.door {
  background: rgba(241, 196, 15, 1);
  background: -moz-linear-gradient(left, rgba(241, 196, 15, 1) 0%, rgba(247, 221, 118, 1) 20%, rgba(245, 219, 115, 1) 26%, rgba(241, 196, 15, 1) 100%);
  background: -webkit-gradient(left top, right top, color-stop(0%, rgba(241, 196, 15, 1)), color-stop(20%, rgba(247, 221, 118, 1)), color-stop(26%, rgba(245, 219, 115, 1)), color-stop(100%, rgba(241, 196, 15, 1)));
  background: -webkit-linear-gradient(left, rgba(241, 196, 15, 1) 0%, rgba(247, 221, 118, 1) 20%, rgba(245, 219, 115, 1) 26%, rgba(241, 196, 15, 1) 100%);
  background: -o-linear-gradient(left, rgba(241, 196, 15, 1) 0%, rgba(247, 221, 118, 1) 20%, rgba(245, 219, 115, 1) 26%, rgba(241, 196, 15, 1) 100%);
  background: -ms-linear-gradient(left, rgba(241, 196, 15, 1) 0%, rgba(247, 221, 118, 1) 20%, rgba(245, 219, 115, 1) 26%, rgba(241, 196, 15, 1) 100%);
  background: linear-gradient(to right, rgba(241, 196, 15, 1) 0%, rgba(247, 221, 118, 1) 20%, rgba(245, 219, 115, 1) 26%, rgba(241, 196, 15, 1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#f1c40f', endColorstr='#f1c40f', GradientType=1);

  box-shadow: 13px -9px 47px -16px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 13px -9px 47px -16px rgba(0, 0, 0, 0.75);
  box-shadow: 13px -9px 47px -16px rgba(0, 0, 0, 0.75);
  top: -5px;
  left: -5px;
  position: absolute;
  width: 15.5rem;
  height: 33rem;
  border-radius: 50px 50px 10px 10px;
  border: 2px solid map_get($colors, "sun");
  font-family: 'Lexend Zetta', sans-serif;
  display: flex;
  justify-content: center;
  cursor: pointer;

  transform-origin: left;
  /*Speed of the Door animation*/
  transition: all 0.6s ease-in-out;
}


@-webkit-keyframes bounce-in-top {
  0% {
    -webkit-transform: translateY(-500px);
    transform: translateY(-500px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
    opacity: 0;
  }
  38% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
    opacity: 1;
  }
  55% {
    -webkit-transform: translateY(-65px);
    transform: translateY(-65px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  72% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  81% {
    -webkit-transform: translateY(-28px);
    transform: translateY(-28px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  90% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  95% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
}

@keyframes bounce-in-top {
  0% {
    -webkit-transform: translateY(-500px);
    transform: translateY(-500px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
    opacity: 0;
  }
  38% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
    opacity: 1;
  }
  55% {
    -webkit-transform: translateY(-65px);
    transform: translateY(-65px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  72% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  81% {
    -webkit-transform: translateY(-28px);
    transform: translateY(-28px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  90% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  95% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
}


span {
  content: "SWEG";
  position: absolute;
  letter-spacing: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: ghostwhite;
  text-shadow: -1px 0 silver, 0 1px silver, 1px 0 silver, 0 -1px silver;
  font-size: 1.5rem;
  //font-weight: bold;
  font-family: 'Lexend Zetta', sans-serif;
  padding-top: 4.4rem;
  padding-left: 2.8rem;
  z-index: 2;

  animation: scale-in-hor-left 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.3s both;

}

@-webkit-keyframes scale-in-hor-left {
  0% {
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: 0% 0%;
    transform-origin: 0% 0%;
    opacity: 1;
  }
  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    -webkit-transform-origin: 0% 0%;
    transform-origin: 0% 0%;
    opacity: 1;
  }
}

@keyframes scale-in-hor-left {
  0% {
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: 0% 0%;
    transform-origin: 0% 0%;
    opacity: 1;
  }
  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    -webkit-transform-origin: 0% 0%;
    transform-origin: 0% 0%;
    opacity: 1;
  }
}


.handler {
  content: "";
  position: absolute;
  width: 7.5rem;
  height: 6.25rem;
  background-color: rgba(226, 226, 226, 1);
  background: -moz-linear-gradient(left, rgba(226, 226, 226, 1) 0%, rgba(254, 254, 254, 1) 20%, rgba(254, 254, 254, 1) 26%, rgba(219, 219, 219, 1) 72%, rgba(219, 219, 219, 1) 77%, rgba(219, 219, 219, 1) 100%);
  background: -webkit-gradient(left top, right top, color-stop(0%, rgba(226, 226, 226, 1)), color-stop(20%, rgba(254, 254, 254, 1)), color-stop(26%, rgba(254, 254, 254, 1)), color-stop(72%, rgba(219, 219, 219, 1)), color-stop(77%, rgba(219, 219, 219, 1)), color-stop(100%, rgba(219, 219, 219, 1)));
  background: -webkit-linear-gradient(left, rgba(226, 226, 226, 1) 0%, rgba(254, 254, 254, 1) 20%, rgba(254, 254, 254, 1) 26%, rgba(219, 219, 219, 1) 72%, rgba(219, 219, 219, 1) 77%, rgba(219, 219, 219, 1) 100%);
  background: -o-linear-gradient(left, rgba(226, 226, 226, 1) 0%, rgba(254, 254, 254, 1) 20%, rgba(254, 254, 254, 1) 26%, rgba(219, 219, 219, 1) 72%, rgba(219, 219, 219, 1) 77%, rgba(219, 219, 219, 1) 100%);
  background: -ms-linear-gradient(left, rgba(226, 226, 226, 1) 0%, rgba(254, 254, 254, 1) 20%, rgba(254, 254, 254, 1) 26%, rgba(219, 219, 219, 1) 72%, rgba(219, 219, 219, 1) 77%, rgba(219, 219, 219, 1) 100%);
  background: linear-gradient(to right, rgba(226, 226, 226, 1) 0%, rgba(254, 254, 254, 1) 20%, rgba(254, 254, 254, 1) 26%, rgba(219, 219, 219, 1) 72%, rgba(219, 219, 219, 1) 77%, rgba(219, 219, 219, 1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#e2e2e2', endColorstr='#dbdbdb', GradientType=1);
  clip-path: polygon(0 30%, 100% 24%, 100% 43%, 0 39%);
  border: 1px solid silver;
  box-sizing: border-box;
  margin-top: 11.9rem;
  margin-left: 7rem;
  z-index: 2;
  animation: scale-in-hor-left 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.3s both;
}


.doorOpen {

  /*prespectiv creates the door open effect*/
  transform: perspective(1200px) translateZ(0px) translateX(0px) translateY(0px) rotateY(-105deg);
}


.hidden {
  display: none;
}


.search_result {
  width: 100%;
  height: 36rem;

  .name_list {
    width: 53rem;
    justify-content: center;
    padding-bottom: 1.25rem;

  }

  .name_list li {
    display: inline-block;
    text-align: justify;
    padding: 2px;
    margin: 2px;
    text-decoration: none;
    background-color: map_get($colors, "violet");
    color: ghostwhite;
    border-radius: 5px;
    font-size: 1.1rem;

  }

  .name_list li:hover {
    cursor: pointer;
    background-color: map_get($colors, "sun");
  }

  h1 {
    color: ghostwhite;
    text-align: center;
    animation: text-focus-in 1s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
  }

}

.recipe_box {
  display: flex;

}

@-webkit-keyframes text-focus-in {
  0% {
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
  }
  100% {
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}

@keyframes text-focus-in {
  0% {
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
  }
  100% {
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}


.list {
  list-style-type: none;
  color: ghostwhite;

}

