    border-radius: 5px;
    background-color: map_get($colors, "violet");
    border: 1px solid map_get($colors, "violet");
    text-align: justify;

  }

  width: 100%;
  align-items: center;
  margin-bottom: 3rem;
  margin-right: 5%;


}


.measure_list {
  width: 20%;
  font-size: 1.2rem;
  padding-bottom: 1.25rem;

}

.ingredient_list {
  width: 30%;
  font-size: 1.2rem;
  padding-bottom: 1.25rem;

}

.cold {
  display: flex;
  justify-content: space-between;
  align-items: center;

}

